.card{
    width: 300px;
    height: 450px;
    color: white;
    -webkit-box-shadow: 0px 10px 20px #131424;
            box-shadow: 0px 10px 20px #131424;
}


.view-btn{
    border: 2px solid transparent;
    -webkit-border-image: -webkit-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
         -o-border-image: -o-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
            border-image: linear-gradient(315deg, #F1572F 0%, #EB216E 74%);
    border-image-slice: 1;
    padding: 15px;
    color: white;
    width: 100px;
    font-weight: bold;
    
    
}

.view-btn:hover{
   
    background-color: #F1572F;
    color: white;
    text-decoration: none;
    border: none;
    background-image: -webkit-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
    background-image: -o-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
    background-image: linear-gradient(315deg, #F1572F 0%, #EB216E 74%);
    
}

.title{
    color: white;
    font-weight: bold;
}

.course-list{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}






