.data-section{
    margin-top: 5%;
    padding: 2%;
    
}

.bg-dark{
    background-color: #27283D !important;
}

.heading , .sub {
    max-width: 80%;
    text-align: left;
    
}

.heading{
    color: white;
}

.sub{
    color: rgb(180, 180, 180);
}



.btn-orange{
    border: 2px solid transparent;
    -webkit-border-image: -webkit-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
         -o-border-image: -o-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
            border-image: linear-gradient(315deg, #F1572F 0%, #EB216E 74%);
    border-image-slice: 1;
    padding: 20px;
    color: white;
    width: 300px;
    font-weight: bold;
    
    
}

.btn-orange:hover{
    background-color: #F1572F;
    color: white;
    text-decoration: none;
    border: none;
    background-image: -webkit-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
    background-image: -o-linear-gradient(135deg, #F1572F 0%, #EB216E 74%);
    background-image: linear-gradient(315deg, #F1572F 0%, #EB216E 74%);
}

.img-section{
    position: relative;
}

.front{
    width: 500px;
    height: 100%;
    margin-top: 100px;
}


@media screen and (max-width:700px) {
    .front{
        width: 80%;
        height: 80%;
        margin-right:10%;
    }
}


