
.img-section2{
    position: relative;
    margin-top: 10%;
}

.overlay{
    background-image: -webkit-linear-gradient(135deg, #22123d 0%, #746aa0 74%);
    background-image: -o-linear-gradient(135deg, #22123d 0%, #746aa0 74%);
    background-image: linear-gradient(315deg, #22123d 0%, #746aa0 74%);
    background-color: #F1572F;
    width: 100%;
    height: 25rem;
    position: absolute;
    z-index: 2;
    opacity: 0.5;
   -webkit-box-shadow: 0px 15px 20px #131424;
           box-shadow: 0px 15px 20px #131424;
}

.course-img{
    width: 100%;
    height: auto;
    position: absolute;
    z-index: 1;

}

.info-card{
    margin: 5%;
    background-color: white;
    height: 30rem;
    width: 100%;
    padding: 20px;
}

.info-card ul li{
    padding: 20px;
}

.info-card ul li a{
    color: rgb(34, 33, 33);
    text-decoration: none;
    background-color: white;
    cursor: pointer;
    padding: 15px;
} 

.info-card ul li a:hover{
    background-color: rgb(231, 231, 231);

}
.info-card ul li a i{
   margin: 5px;
} 

.instructor{
    width: 150px;
    height: 150px;
    border-radius: 100% ;
    margin: 0.7rem;
}

.tab-pane{
    padding-left: 20px;

}

.tab-pane h3{
    margin-top: 20px;
    margin-left: 10px;
    font-weight: bold;
}

.tab-pane p{
    margin-left: 10px;
}

@media screen  and (max-width:1200px){
    .info-card{
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;

    }
    
}

@media screen  and (max-width:770px){
    .info-card{
        margin-top: 100%;
        
    }
    
}

@media screen  and (max-width:575px){
    
   .img-section{
       width: 20rem;
       margin-left: 22%;
   }
   .info-card{
       width: 20rem;
       margin-left: 25%;
   }
    
}

@media screen  and (max-width:500px){
    .overlay{
        height: 15rem;
    }
    .img-section{
        width: 18rem;
        margin-left: 15%;
        
    }
    .info-card{
        width: 18rem;
        margin-left: 15%;
       
    }
     
 }

 @media screen  and (max-width:400px){
   
    .img-section{
        width: 15rem;
        margin-left: 10%;
        
    }
    .info-card{
        width: 15rem;
        margin-left: 10%;
       
    }
     
 }

 @media screen  and (max-width:300px){
    
    .img-section{
        width: 13rem;
        margin-left: 8%;
        
    }
    .info-card{
        width: -webkit-fit-content;
        width: -moz-fit-content;
        width: fit-content;
        margin-left: 8%;
       
    }
     
 }

  






